import React from 'react';
import {Row, Container, Col} from 'react-bootstrap';
import './App.css';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {Link} from 'react-router-dom';

const footer = () =>{
    return(
        <Container className="footer"  fluid>
          <div>
              <Row>
                <Col lg={true}>
                  <img 
                    src="https://www.pngitem.com/pimgs/m/87-877725_recipes-png-transparent-png.png"
                    width="130px"
                    height="90px"
                    className="d-inline-block align-top"
                    alt="recipe logo"
                  />
                  <h3>View <span>Recipes</span></h3>
                  <p>
                    <Link to="/details/Breakfast">Breakfast</Link>|
                    <Link to="/details/Dinner">Lunch/Dinner</Link>|
                    <Link to="/details/Dessert">Dessert</Link>|
                  </p>
                  <p> © 2020 Recipes Pvt. Ltd.</p>
                </Col>
                <Col lg={true}>
                    <h3>Follow Us
                      <br/>
                      <span><FontAwesomeIcon className="faicons" icon={['fab', 'facebook']} /></span>|
                      <span><FontAwesomeIcon className="faicons" icon={['fab', 'instagram']} /></span>|
                      <span><FontAwesomeIcon className="faicons" icon={['fab', 'twitter']} /></span>
                    </h3>
                </Col>
                <Col lg={true}>
                    <p>
                      <span>About the Brand</span>
                      We offer free access to cooking recipes of myriads of types and categories 
                      so that anyone may enjoy different cuisines found in the World
                    </p>
                </Col>
              </Row>
          </div>
        </Container>
    )
}

export default footer;