import React, { Component } from 'react';
import { Carousel } from 'react-bootstrap';
import {Link} from 'react-router-dom';
 
class DemoCarousel extends Component {
    sliderStyle = {
        height: "500px",
        width: "90%"
    }
    render() {
        return (
            <Carousel>
                <Carousel.Item>
                    <Link to="/details/Marsala"><img
                    className="d-block w-100"
                    src="https://i2.wp.com/www.savorysimple.net/wp-content/uploads/2018/05/Easy-Chicken-Marsala-040.jpg"
                    alt="First slide"
                    style={this.sliderStyle}
                    /></Link>
                    <Carousel.Caption>
                        <h3>Chicken Marsala</h3>
                        <p>A delicious, classic chicken dish -- lightly coated chicken breasts braised with Marsala wine and mushrooms. Easy and ideal for both a quick weeknight entree AND serving to company.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <Link to="/details/French-Toast"><img
                    className="d-block w-100"
                    src="https://hercanberra.com.au/wp-content/uploads/2016/10/Frangelico-French-Toast-with-Blueberries-and-Vanilla-Bean-Creme-Fraiche-Anisa-Sabet-The-Macadames-16-11-scaled.jpg"
                    alt="Second slide"
                    style={this.sliderStyle}
                    /></Link>
                    <Carousel.Caption>
                        <h3>French Toast</h3>
                        <p>With recipes for overnight French toast or the classic cinnamon French toast, you'll know how to make French toast in no time.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <Link to="/details/pizza"><img
                    className="d-block w-100"
                    src="https://westcottu.com/wp-content/uploads/Screen-Shot-2015-04-23-at-11.03.03-AM.png"
                    alt="Third slide"
                    style={this.sliderStyle}
                    /></Link>
                    <Carousel.Caption>
                        <h3>Italian Pizza</h3>
                        <p>A delicious, Italian Styled dish -- Crispy brown Crust and Warm tasty oozing cheese You are sure to love making it and eating it</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <Link to="/details/cheese-cake"><img
                    className="d-block w-100"
                    src="https://christiannkoepke.com/wp-content/uploads/2017/05/Photography-by-Christiannkoepke.com-3-2.jpg"
                    alt="Fourth slide"
                    style={this.sliderStyle}
                    /></Link>
                    <Carousel.Caption>
                        <h3>Cheese Cake </h3>
                        <p>Find easy and decadent cheesecake recipes, from creamy, smooth chocolate to light 
                            and fresh strawberry, Recipes has all your favorites in one place.</p>
                    </Carousel.Caption>
                </Carousel.Item>

            </Carousel>
        );
    }
};
 
export default DemoCarousel;