import React,{useEffect,useState} from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Navbar, Nav, Form,FormControl} from 'react-bootstrap';
import {Link,Redirect} from 'react-router-dom';
import './App.css';
//import {Link} from 'react-router-dom';


const NaviBar = () =>{
    const APP_ID = '4b2bcae5';
  const APP_KEY = '95f65fbfb8898ba34648215df76b38ed';
  const [recipes, setRecipes] = useState([]);
  const [search,setSearch] = useState('');
  const [query, setQuery]= useState('beef');


  const getRecipes = async () => {
    const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`);
    const data = await response.json();
    setRecipes(data.hits);
    console.log(data.hits);
  }

  const getSearch = e =>{
    e.preventDefault();
    setQuery(search);
    window.location.href = `/React-Recipe/#/details/${search}`;
    setSearch('');
  }
  const updateSearch = e => {
    setSearch(e.target.value);
    //console.log(search);
  };
    return(
        <Navbar bg="dark" expand="lg">
        <Navbar.Brand href="#home">
            <Link to="/"><img 
                src="https://www.pngitem.com/pimgs/m/87-877725_recipes-png-transparent-png.png"
                width="130px"
                height="90px"
                className="d-inline-block align-top"
                alt="recipe logo"
            /></Link>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav"  />
        <Navbar.Collapse id="basic-navbar-nav">
            <Nav id="order" className="mr-auto">
            <Link   to="/">Home</Link>
            <Link   to="/details/">Random</Link>
            </Nav>
            <Form onSubmit={getSearch} inline>
                <div className="input-icons">
                    <button>
                        <FontAwesomeIcon className="faicons" icon="search"/>
                    </button>
                    <FormControl type="text" placeholder="Search" value={search} onChange={updateSearch} id="searchBar" className="mr-md-3" />
                </div>
            </Form>
        </Navbar.Collapse>
        </Navbar>
    )
}

export default NaviBar;

