import React,{useEffect, useState} from 'react';
import Details from './Details';
import './App.css';
import {Link} from 'react-router-dom';


const RecipeDetails = ({match}) =>{
    const APP_ID = '4b2bcae5';
  const APP_KEY = '95f65fbfb8898ba34648215df76b38ed';
  const [recipes, setRecipes] = useState([]);
  const [search,setSearch] = useState('');
  const [query, setQuery]= useState(match.params.label);

  useEffect(() =>{

    let food = ["beef","chicken","tuna","salad","salmon","English muffin","Cupcake","Cookie","Cheese cake","cake",
                "Chinese","Italian","French Toast","Shrimp","Lobster","Lamb","rice","potato","sweet potato","Ham","Apple",
                "Ice Cream","Bacon","Eggs","Cherry","Pancake","Waffle","brownie","bagel","jam","Jamaica"];
    let choice = Math.floor(Math.random() * (food.length+1) );
    console.log(food[choice]);
    let request = "";
    typeof match.params.label === "undefined" ?  request = food[choice]: request = match.params.label
    getRecipes(request);
  },[]);

  const getRecipes = async (ch) => {
    const response = await fetch(`https://api.edamam.com/search?q=${ch}&app_id=${APP_ID}&app_key=${APP_KEY}`);
    const data = await response.json();
    setRecipes(data.hits);
    console.log(match.params.label);
    console.log(data.hits);
  }

  const getSearch = e =>{
    e.preventDefault();
    setQuery(search);
    setSearch('');
  }
  const updateSearch = e => {
    setSearch(e.target.value);
    //console.log(search);
  };

  return(
      <div className="App">
      <div className="recipes">
      {recipes.map(recipe => (
            <Details title={recipe.recipe.label} calories={recipe.recipe.calories} 
                url = {recipe.recipe.url} image={recipe.recipe.image} key={recipe.recipe.label} ingredients={recipe.recipe.ingredients}
            />
            ))}
      </div>
      </div>
  )
}

export default RecipeDetails;